                    self.invoiceDetailsDup.push(invoice);
                    this.selected = []
                }
            });
        },
        handleTableExchangeRateFilterMode(str) {
            let self = this;
            self.invoiceDetailsDup = [];
            this.isHidden64=1;
            self.invoiceDetails.map(invoice => {
                if(invoice.exchangeRate.toLowerCase().includes(str.toLowerCase())) {
                    self.invoiceDetailsDup.push(invoice);
                    this.selected = []
                }
            });
        },

    },
   

}
</script>

