/*====================================== Checkbox css start here========================== */
.form-checkbox-Shipment{
    display: inline;
    position: relative;
    padding-left: 35px;
    cursor: pointer;
    font-size: 22px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}
.form-checkbox-Shipment input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
    border: 1px solid #a8b6d1;

}
.checkmark {
    position: absolute;
    left: 0;
    height: 17px;
    width: 17px;
    border: 1px solid #a8b6d1;
    border-radius: 5px;
    background-color: #FFFFFF;
}
.form-checkbox-Shipment input:checked ~ .checkmark {
    background-color: #1f6bff;
}
.checkmark:after {
    content: "";
    position: absolute;
    display: none;
}

.form-checkbox-Shipment input:checked ~ .checkmark:after {
    display: block;
}

.form-checkbox-Shipment .checkmark:after {
    left: 5px;
    top: 4px;
    width: 5px;
    height: 7px;
    border: solid white;
    border-width: 0 2px 2px 0;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
}
/* =====================================Checkbox css ends here ===============================*/



