<script>
    export default {
        props: {
            mode: {
                type: String,
                default: null
            }
        },
        data() {
            return {
                // ------------------ text field validation ---------------
                isValidCleanerName: 0,
                isValidDriverName: 0,
                isValidPlaceOfDelivery: 0,
                options: [
                    { key: null, value: null, text: 'Please select an option', disabled: true },
                    { key: "a", value: 'a', text: 'Selected Option 1' },
                    { key: "b", value: 'b', text: 'Selected Option 2' },
                    { key: "c", value: 'c', text: 'Selected Option 3' }
                ],
                cargoAdviseRecalculate: {
                    totalNoOfCtns: "",
                    totalNetWeight: "",
                    totalGrossWeight: "",
                    totalCBM: "",
                    totalCommisionValue: "",
                    grandTotal: "",
                    totalShipQty: "",
                    qtyPcs: ""
                },
                cargoAdviseShipmentDetails: {
                    expoter: "",
                    salesCompany: "",
                    shipmentMode: "",
                    exFactoryDate: "",
                    unitPrice: ""
                },
                perShipmentRecalculate: {
                    totalNoOfCTNS: "",
                    totalCBM: "",
                    totalNetWeight: "",
                    totalCommisionValue: "",
                    totalGrossWeight: "",
                    grandTotal: "",
                    noOfPCS: "",
                    shipQty: "",
                },
                perShipmentBasicInvoice: {
                    buyerName: "",
                    destinationCountry: "",
                    consignee: "",
                    expoter: "",
                    voyageNo: "",
                    vesselNo: "",
                    etd: "",
                    portOfDischarging: "",
                    modeOfShipment: "",
                    finacialYear: "",
                    quoteYear: "",
                    paymentTerms: "",
                    forwarder: "",
                    portOfLoading: "",
                    exchangeRate: "",
                    boockingNo: "",
                    cusdecNumber: "",
                    placeOfDelivery: ""
                },
                preShipmentPackingRecalculate: {
                    totalCbm: "",
                    totalNetWeightKgs: "",
                    totalGrossWeightKgs: "",
                    totalNoOfCtns: "",
                    totalNoOfPiecs: "",
                    totalToBeShipQty: "",
                    totalNetWeightKgsPre: "",
                    totalGrossWeightKgsPre: ""
                },
                preShipmentPackingData: {
                    lorryNo: "",
                    tailorNo: "",
                    containerNo: "",
                    sealNo: "",
                    tareWeight: "",
                    boockingNo: "",
                    driverName: "",
                    driverNic: "",
                    cleanerName: "",
                    cleanerNic: ""
                }
            };
        },
        watch: {
            mode: {
                handler(val) {
                    if (val) {
                        return val;
                    }
                },
                immediate: true,
                deep: true
            }
        },
        computed: {
            // ----------------- form validation ---------------------
            formIsValidCargoAdviceRecalculate() {
                return (
                    this.cargoAdviseRecalculate.totalNoOfCtns
                );
            },
            formIsValidCargoAdviceShipmentDetails() {
                if (
                    this.isValidUnitPrice === 0
                ) {
                    return (
                        this.cargoAdviseShipmentDetails.unitPrice
                    );
                } else {
                    return false;
                }
            },
            formIsValidPreShipmentRecalculate() {
                return (
                    this.perShipmentRecalculate.totalNoOfCTNS
                );
            },
            formIsValidPreShipmentInvoiceDetails() {
                if (
                    this.isValidPlaceOfDelivery === 0
                ) {
                    return (
                        this.perShipmentBasicInvoice.voyageNo
                    );
                } else {
                    return false;
                }
            },
            formIsValidPreShipmentPackingRecalculate() {
                return (
                    this.preShipmentPackingRecalculate.totalCbm
                );
            },
            formIsValidPreShipmentPackingData() {
                if (
                    this.isValidDriverName === 0
                ) {
                    return (
                        this.preShipmentPackingData.lorryNo
                    );
                } else {
                    return false;
                }
            }
        },
        created() {},
        methods: {
            handleValidation(type, value, field) {
                if (type === "char") {
                    this.isLetter(value, field);
                }
            },
            isLetter(value, field) { // ------------ regular expression for check value include only char ------------ 
                if (value.length !== 0) {
                    if(/^[A-Za-z]+$/.test(value)) { // ---------------- regx return true -------------------
                        if (field === "isValidPlaceOfDelivery") {
                            this.isValidPlaceOfDelivery = 0;
                        } else if (field === "isValidDriverName") {
                            this.isValidDriverName = 0;
                        } else if (field === "isValidCleanerName") {
                            this.isValidCleanerName = 0;
                        }
                    } else { // ---------------- regx return false -------------------
                        if (field === "isValidPlaceOfDelivery") {
                            this.isValidPlaceOfDelivery = 1;
                        } else if (field === "isValidDriverName") {
                            this.isValidDriverName = 1;
                        } else if (field === "isValidCleanerName") {
                            this.isValidCleanerName = 1;
                        }
                    }
                } else { // ---------------- when value getting empty -------------------
                    if (field === "isValidPlaceOfDelivery") {
                        this.isValidPlaceOfDelivery = 0;
                    } else if (field === "isValidDriverName") {
                        this.isValidDriverName = 0;
                    } else if (field === "isValidCleanerName") {
                        this.isValidCleanerName = 0;
                    }
                }
            }
        }
    }
</script>